html {
  box-sizing: border-box;
}
*,
button
{
  box-sizing: inherit;
}

.app-wrapper {
  display: grid;
  grid-template-areas:
    "h h"
    "m c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
}

.app-wrapper-content {
  grid-area: c;
  background-color: yellow;
}